
.PostCards {
  display: flex;
  flex-direction: column;
  align-items: left;
}

.PostsTitle {
  width:500px;
  align-self: center;
  text-align: left;
}


.PostCard {
  width:500px;
  text-align: left;
  align-self: center;
  margin-bottom: 12px;
}

.PostCardTitle  {
  text-align: left;
  font-weight: bold;
}

.PostCardDate {
  text-align: left;
  font-size: smaller;
  color: silver;
  margin-top: 2px;
}

.PostCardAbstract {
  text-align: left;
}

/* So the readmore link can be after the abstract text*/
.PostCardAbstract * {
  display: inline;
}

.PostCardReadmore {
  color: silver;
  display: inline;
  font-style: italic;
}

.error {
  background-color: red;
}
